.skillContainer{
    display: flex;
    width:80%;
    background-color: white;
    margin-left:auto;
    margin-right:auto;
    margin-bottom:16px;
    border-radius: 50px;
    border:2px solid white;
    

}
.skillCard{
    display:inline-block;
}
.skillImage{
    width:100%;
    opacity: .96;

}
.skillSkill{
    display: block;
    width:100%;
    text-transform: uppercase;
    font-size:2em;
    background-color: red;
    margin-top: 0px;
    border-top-right-radius: 50px;
    border-top-left-radius: 50px;
    padding-top:10px;
    padding-bottom: 10px;
    

}
.skillExperience{
    display: block;
    width: 100%;
    text-transform: uppercase;
    font-size:1.5em;
    margin-bottom: 0px;
    background-color: red;
    padding-top: 20px;
    padding-bottom: 20px;
    border-bottom-left-radius: 50px;
    border-bottom-right-radius: 50px;

}
.skillCertification{
    
}

@media(min-width:320px){
    .skillExperience{
        font-size: 1em;
    }
}
@media(min-width:768px){
    .skillExperience{
        font-size: 1.5em;
    }
}