.certificateContainer{
    display: flex;
    width:80%;
    background-color: white;
    margin-left:auto;
    margin-right:auto;
    margin-bottom:16px;
    border-radius: 50px;
    border:2px solid white;

}
.certificateCard{
    display:inline-block;

}
.certificateTitle{
    display: block;
    width:100%;
    text-transform: uppercase;
    text-align: center;
    background-color: red;
    margin-top:0px;
    border-top-right-radius: 50px;
    border-top-left-radius: 50px;
    padding-top:10px;
    padding-bottom: 10px;

}
.certificateText{
    display: inline-block;
    width:80%;
    font-size:2em;

}
.certificateImage{
    width:100%;
    opacity: .96;
    border-bottom-right-radius: 50px;
    border-bottom-left-radius: 50px;

}
@media(max-width:590px){
    .certificateText{

        font-size: 1.3em;
    }

}

